<template>
    <div class="profile-page">
        <section class="section-hero section-shaped my-0">
            <!-- <div class="shape shape-style-1 shape-primary shape-skew alpha-4">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div> -->
            <particles-bg class="background-grad-logo shape shape-skew" type="cobweb" color="#FFFFFF" :num="170"/>

            <div class="container shape-container d-flex align-items-center">
                <div class="col px-0">
                    <div class="row justify-content-center align-items-center">
                            <img src="img/brand/headlines/voice-generation.png" style="width: 800px; padding-bottom: 80px;" class="img-fluid">                        
                    </div>
                </div>
            </div>

        </section>
        <section class="section section-skew">
            <div class="container">

                <card shadow class="card-profile mt--300" no-body>
                    <div class="px-4">
                        <div class="row justify-content-center">
                            
                            <!-- <route-head title='Voice Description' style="margin-top: 10vw; font-size: 2.5vw;"></route-head> -->
                             <!-- <h1 class="routerhead headfont" style="margin-top: 10vw; font-size: 3vw;">Voice Description</h1> -->

                            <!-- image -->
                            <face-image :loading="generateClicked" :face="faceimg"></face-image>
                            
                            <!-- mic icon -->
                            <mic-button @clicked="onClickMic"></mic-button>
                            
                            <!-- <vue-speech v-if="this.micClicked === 1" hidden @onTranscriptionEnd="onEnd" :key="NaN" />                         -->
                            
                            <!-- textarea -->
                            <!-- <description-box :descriptionArr="[1]" :audioDescription="description" :descriptionLength="descriptionLength"></description-box> -->
                            <textarea v-model="description" placeholder="Write the face description here!"></textarea>
                            
                            <!-- <base-button class="btn-3" type="primary" icon="fa fa-paint-brush">Generate</base-button> -->
                            <generate-button :doneGeneration="done" @clicked="onClickGenerate"></generate-button>
                        </div>


                    </div>
                </card>
            </div>
        </section>
    </div>
</template>
<script>
import FaceImage from './components/FaceImage.vue';
import RouteHead from './components/RouteHead.vue';
import DescriptionBox from './components/DescriptionBox.vue';
import MicButton from './components/MicButton.vue';
import GenerateButton from './components/GenerateButton.vue';
import axios from 'axios'



export default {
  components: { 
      FaceImage,
      DescriptionBox,
      MicButton,
      RouteHead,
      GenerateButton
  },
  data: function() {
    return{
        description: '',
        descriptionLength: 0,
        micClicked: 0,
        generateClicked: false,
        faceimg: '',
        done: true
      }
    },
  
  created() {
    this.recognition =  new webkitSpeechRecognition();
    this.recognition.continuous = true;
    this.recognition.interimResults = false;
    this.recognition.lang = 'en-US';
    this.recognition.maxAlternatives = 5;

    this.recognition.onresult = event => {
        this.description = this.description + event.results[event.results.length - 1][0].transcript;
    };
  },

  methods: {
    onClickMic (micClicked) {
      if (micClicked == 1){
        this.recognition.start();
      }

      if (micClicked == 0){
        this.recognition.stop();
      }
      this.micClicked = micClicked;
    },

    onClickGenerate (generateClicked) {
      this.generateClicked = generateClicked
      this.done = false;
      axios.post(this.$IP + 'tgenerate', {text: this.description})
        .then(function( response ){
            // Handle success
          console.log(response.data.values);
          this.faceimg = response.data.face;
          this.generateClicked = false;
          this.done = true;
          
        }.bind(this));
      
      
    }
  }
  };
</script>
