
<template>
    <!-- <section class="section section-components pb-0" id="section-components"> -->
        <div class="container">
            <div class="row justify-content-center">
                <div class="col-lg-12">
                    
                    <!-- <base-button class="btn-3 tab6_rec" type="primary" icon="fa fa-paint-brush"  size="lg"></base-button> -->
                    <base-button style="background: #005ba9;" type="primary" size="lg" class="btn-3 generate_button" icon="fa fa-paint-brush" :disabled="this.clicked" @click="handleClick" >Generate</base-button>
                </div>
            </div>
        </div>
    <!-- </section> -->

</template>




<script>
import axios from 'axios'
export default {
    name: "generate-button",
    data: function() {
    return{
        clicked: false
      }
    },
    props: {
        doneGeneration: Boolean 
    },
    watch: { 
        doneGeneration(newVal, oldVal) { // watch it
            console.log(this.clicked)
            this.clicked = !newVal;
        }
    },
    mounted(){
        this.clicked = false;
    },
    methods: {
        handleClick() {
            this.clicked = !this.clicked
            this.$emit('clicked', this.clicked);
        }
    }
};
</script>

